import {
  Button,
  Card,
  CardBody,
  Input,
  Pagination,
  Select,
  Table,
  TableCell,
  TableContainer,
  TableFooter,
  TableHeader,
} from "@windmill/react-ui";
import { useContext, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";

//internal import
import useAsync from "hooks/useAsync";
import useFilter from "hooks/useFilter";
import OrderServices from "services/OrderServices";
import ordersfromlocal from "../../utils/vendorsInvoceData";
import NotFound from "components/table/NotFound";
import PageTitle from "components/Typography/PageTitle";
import { SidebarContext } from "context/SidebarContext";
import OrderTable from "components/InvoiceDetails/InvoiceDetailsTable";
import TableLoading from "components/preloader/TableLoading";
import { FiPlus } from "react-icons/fi";
import { Link } from "react-router-dom";
import { useParams } from "react-router";
import { getAllInvoice } from "services/Api";
const Orders = () => {
  const {
    time,
    currentPage,
    searchText,
    status,

    handleSubmitForAll,
    resultsPerPage,
    startDate,
    endDate,
    lang,
  } = useContext(SidebarContext);
  const { t } = useTranslation();
  const [paymentType, setPaymentType] = useState("paid");
  const [pageNumber, setPageNumber] = useState(1);
  const [InvoiceData, setInvoiceData] = useState({});
  const [loading, setLoading] = useState(false);
  const [wholeCount, setwholeCount] = useState({});
  const { id } = useParams();
  const getVenderInvoices = async (
    paymentType,
    pageNumber,
    id,
    search = ""
  ) => {
    const res = await getAllInvoice(pageNumber, search);
    if (res.status === 200) {
      setInvoiceData(res?.data?.data);
      console.log(res?.data?.data, "resresres");
      setwholeCount(res?.data?.data?.pagination);
    }
    setLoading(false);
  };

  useEffect(() => {
    setLoading(true);
    getVenderInvoices(paymentType, pageNumber, id);
  }, [paymentType]);

  const handleChangePage = (page) => {
    getVenderInvoices(paymentType, page, id);
    setPageNumber(page);
  };

  const handleSearch = (search) => {
    console.log(search, "search");
    getVenderInvoices(paymentType, pageNumber, id, search);
  };

  return (
    <>
      <PageTitle>{t("All Invoice")}</PageTitle>
      <Card className="min-w-0 shadow-xs overflow-hidden bg-white dark:bg-gray-800 mb-5">
        <CardBody className="">
          <form
            onSubmit={handleSubmitForAll}
            className="py-3 md:pb-0 grid gap-4 lg:gap-6 xl:gap-6  xl:flex"
          >
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <Input
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                type="search"
                onChange={(e) => handleSearch(e.target.value)}
                name="search"
                placeholder={t("StaffSearchBy")}
              />
              <button
                type="submit"
                className="absolute right-0 top-0 mt-5 mr-1"
              ></button>
            </div>
            <div className="lg:flex  md:flex xl:justify-end xl:w-1/2  md:w-full md:justify-start flex-grow-0">
              {/* <div className="w-full md:w-48 lg:w-48 xl:w-48">
                  <Link to={`/add-invoice/${id}`}>
                    {" "}
                    <Button className="w-full rounded-md h-12">
                      <span className="mr-2">
                        <FiPlus />
                      </span>
                      {t("Add Invoice")}
                    </Button>{" "}
                  </Link>
                </div> */}
            </div>
          </form>
        </CardBody>
      </Card>
      {/* <div className="w-full md:w-48 lg:w-48 xl:w-48 mb-5">
          <Select
            className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
            value={paymentType}
            onChange={(e) => setPaymentType(e.target.value)}
          >
            <option value="paid">Paid</option>
            <option value="unpaid">Unpaid</option>
          </Select>
        </div> */}
      {loading ? (
        <TableLoading row={12} col={7} width={160} height={20} />
      ) : InvoiceData?.vendor_invoice?.length !== 0 ? (
        <TableContainer className="mb-8 dark:bg-gray-900">
          <Table>
            <TableHeader>
              <tr>
                <TableCell>{t("S.No")}</TableCell>
                <TableCell>{t("Property Details")}</TableCell>
                <TableCell>{t("package Details")}</TableCell>
                <TableCell>{t("Event Details")}</TableCell>
                <TableCell>{t("User Details")}</TableCell>
                <TableCell>{t("Invoice No")}</TableCell>
                <TableCell>{t("Invoice Amount")}</TableCell>
                <TableCell>{t("Invoice Date")}</TableCell>
                <TableCell className="text-right">{t("Action")}</TableCell>
              </tr>
            </TableHeader>

            <OrderTable
              lang={lang}
              orders={InvoiceData?.invoices}
              currency={"$"}
              wholeCount={wholeCount}
            />
          </Table>

          <TableFooter>
            <Pagination
              totalResults={InvoiceData?.pagination?.total}
              resultsPerPage={InvoiceData?.pagination?.pageSize}
              onChange={handleChangePage}
              label="Table navigation"
            />
          </TableFooter>
        </TableContainer>
      ) : (
        <NotFound title="Sorry, There are no invoices right now." />
      )}
    </>
  );
};

export default Orders;
