import { TableBody, TableCell, TableRow } from "@windmill/react-ui";
import PrintReceipt from "components/form/PrintReceipt";
import SelectStatus from "components/form/SelectStatus";
import Status from "components/table/Status";
import Tooltip from "components/tooltip/Tooltip";
import { useTranslation } from "react-i18next";
import { FiEdit, FiPrinter, FiTrash2, FiZoomIn } from "react-icons/fi";
import { Link } from "react-router-dom";
import { showDateTimeFormat } from "utils/dateFormate";
import image1 from "../../assets/img/day1.png";
import image2 from "../../assets/img/day2.png";
import useToggleDrawer from "hooks/useToggleDrawer";
import DeleteModal from "components/modal/DeleteModal";
import moment from "moment";
const OrderTable = ({ orders, currency, globalSetting,wholeCount }) => {
  console.log(orders,"orders")
  const { t } = useTranslation();
  const { title, serviceId, handleModalOpen, handleUpdate } = useToggleDrawer();

  return (
    <>
      <DeleteModal id={serviceId} title={title} />
      <TableBody className="dark:bg-gray-900">
        {orders?.map((order, i) => (
          <TableRow key={i + 1}>
            <TableCell>
            <span className="text-sm">{(wholeCount?.currentPage-1)*wholeCount?.pageSize+i+1}</span>
            </TableCell>

            <TableCell>
              <span className="text-sm">{order?.Booking?.Booking_property?.name}</span>
            </TableCell>

            <TableCell className="text-xs">
              <span className="text-sm">{order?.Booking?.Booking_package?.name}</span>
            </TableCell>

            <TableCell>
              <span className="text-sm">
                {order?.Booking?.Booking_event?.name}
              </span>
            </TableCell>

            <TableCell>
              <span className="text-sm">
                {order?.User?.name}
              </span>
            </TableCell>
            <TableCell>
              <span className="text-sm">
                {order?.invoice_number}
              </span>
            </TableCell>
            <TableCell>
            <span className="text-sm">
                {"GST 5%: " + order?.gst_5}
              </span>
              <br />
              <span className="text-sm">
                {"GST: 18%: " + order?.gst_18}
              </span><br/>
              <span className="text-sm">
                {"Total: " + order?.total}
              </span>
            </TableCell>
            <TableCell>
              <span className="text-sm">
                {order?.createdAt &&
                  moment(order?.createdAt).format("DD-MM-YYYY")}
              </span>
            </TableCell>
            <TableCell className="text-right flex justify-end">
              <div className="flex justify-between items-center">
                {/* <PrintReceipt orderId={order.booking_id} /> */}

                <span className="p-2 cursor-pointer text-gray-400 hover:text-green-600">
                  <Link to={`/allInvocie/${order.booking_id}`}>
                    <Tooltip
                      id="view"
                      Icon={FiPrinter}
                      title={t("ViewInvoice")}
                      bgColor="#059669"
                    />
                  </Link>
                </span>
                {/* <span className="p-2 cursor-pointer text-gray-400 hover:text-green-600">
                  <Link to={`/edit-invoice/${order.id}`}>
                    <Tooltip
                      id="view"
                      Icon={FiEdit}
                      title={t("ViewInvoice")}
                      bgColor="#059669"
                    />
                  </Link>
                </span> */}
                {/* <button
                  onClick={() =>
                    handleModalOpen(order.id, "Invoice", "product")
                  }
                  className="p-2 cursor-pointer text-gray-400 hover:text-red-600 focus:outline-none"
                >
                  <Tooltip
                    id="delete"
                    Icon={FiTrash2}
                    title={t("Delete")}
                    bgColor="#EF4444"
                  />
                </button> */}
              </div>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </>
  );
};

export default OrderTable;
